body{
    background-color: cornflowerblue;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.panel{
    position: absolute;
    background-color: lightgoldenrodyellow;
    border: 8px solid burlywood;
    -webkit-box-shadow: 7px 5px 15px 3px rgba(0,0,0,0.9);
    box-shadow: 7px 5px 15px 3px rgba(0,0,0,0.9);
    z-index: 1;
}
.header{
    left: 40%;
    top: 25px;
    height: 80px;
    width: 370px;
    margin-left: -120px;
}

.header-text{
    font-size: 30px;
    font-weight: bold;
    width: 220px;
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50% , -50%);
    transform:translate(-50% , -50%);
}

.footer{
    left: 40%;
    top: 590px;
    height: 80px;
    width: 380px;
    margin-left: -120px;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.footer button{
    margin-top: 15px;
    margin-left: 90px;
    width: 200px;
    height: 50px;
    background-color: lightblue;
    font-size: 22px;
    -webkit-box-shadow: 3px 5px 10px 2px rgba(0,0,0,0.9);
    box-shadow: 3px 5px 10px 2px rgba(0,0,0,0.9);
    border: 5px solid chocolate;
    font-weight: bold;
}

.footer button:hover{
    background-color: chocolate;
}

.gameBoard {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(4, 1fr);
    padding: 20px;
    width: 480px;
    position: absolute;
    left: 50%;
    margin-left: -25%;
    top: 15%;
    background-color: bisque;
    border: 8px solid burlywood;
    border-radius: 8%;
    -webkit-box-shadow: 7px 5px 15px 3px rgba(0,0,0,0.9);
    box-shadow: 7px 5px 15px 3px rgba(0,0,0,0.9);
}

.gameCircle {
    width: 80px;
    height: 80px;
    margin: 8px;
    border-radius: 50%;
    border: 5px solid black;
}

.player_0 {
    background-color: white;
}

.player_1 {
    background-color: red;
}

.player_2 {
    background-color: blue;
}